<template lang="html">
    <div class="text-input"
        :class="{'has-error': error}">
        <input
               v-bind="$attrs"
               v-on="$listeners"
               class="form-control"
               :value="currentValue"
               :disabled="!active"
               :maxlength="maxlength"
               @input="updated($event.target.value)"/>
        <small v-if="error" class="help-block">{{error}}</small>

    </div>
</template>

<script>
import inputElement from '../../mixins/inputElement.js'
import validatedElement from '../../mixins/validatedElement.js';

export default {
    props: {
        maxlength: Number
    },
    mixins: [inputElement, validatedElement]
}
</script>



