<template lang="html">
    <base-button
        class="cancel-button"
        :btnClass="'btn btn-danger'"
        @click.native="cancelTask">
        Cancel Task
    </base-button>
</template>

<script>
import BaseButton from './BaseButton.vue';
import { mapModuleState } from '../../store/util'
import { getNamespace } from '../../bus'

const pybossa = window.pybossa;

export default {
    components: { BaseButton },

    methods: {
        cancelTask () {
            pybossa.cancelTask(this.projectName, this.taskId);
        }
    },

    computed: {
        ...mapModuleState(
            getNamespace,
            [
                'taskId',
                'projectName'
            ]
        )
    }
}
</script>

<style lang="css">
</style>



