async function fetchEncryptedJson (task) {
    const encryptedPayload = task.info.private_json__encrypted_payload;
    if (encryptedPayload) {
        const signature = task.signature;
        const url = `/fileproxy/encrypted/taskpayload/${task.project_id}/${task.id}`;
        const decryptedJson = await fetchRequestJson(url, signature);
        Object.assign(task.info, decryptedJson);
    }
}

async function fetchPrivateJson (task) {
    const urls = [
        task.info.private_json__upload_url,
        task.info.private_json__workflow_url
    ].filter(url => url && url.length > 0);

    await Promise.all(urls.map(async (url) => {
        const signature = task.signature;
        return fetchRequestJson(url, signature);
    })).then(
        values => values.forEach(v => Object.assign(task.info, v)),
        err => console.log('Error reading files ', urls, err)
    );
}

async function fetchRequestJson (fileUrl, signature) {
    const url = new URL(fileUrl, window.location.origin);
    if (signature) {
        url.searchParams.append('task-signature', signature);
    }
    return fetchJson(url);
}

async function fetchJson (url) {
    const response = await fetch(url);
    if (!response.ok) {
        throw new Error('An error occurred while fetching json file.');
    }
    return response.json();
}

export { fetchPrivateJson }
export { fetchEncryptedJson }



