class ValidationError extends Error {
    constructor (...args) {
        super(...args);
        this.name = 'ValidationError';
    }
}
function error (condition, message) {
    if (!condition) {
        throw new ValidationError(message);
    }
}
const registeredValidations = {
    native: []
};
const isUrl = string => {
    try { return Boolean(new URL(string)); } catch (e) { return false; }
}
const isEmpty = (value) => {
    return (
        value === '' ||
        (!value && value !== 0) ||
        (Array.isArray(value) && value.length < 1) ||
        Object.keys(value).length < 1
    );
}
// isEmpty(0)           = False
// isEmpty(1)           = False
// isEmpty("1")         = False
// isEmpty(["1"])       = False
// isEmpty({a: "1"})    = False
// isEmpty(null)        = True
// isEmpty(undefined)   = True
// isEmptu("")          = True
// isEmpty([])          = True
// isEmpty({})          = True

const validationFactory = {
    clean (getValue, setError) {
        return function () {
            setError('');
            return true;
        }
    },
    required (getValue, setError, isVisible) {
        const error = 'This value is required';
        return function () {
            const value = getValue();
            if (isVisible() && isEmpty(value)) {
                setError(error);
                return false;
            }
            return true;
        }
    },
    number (getValue, setError, isVisible) {
        const error = 'This value must be a number';
        return function () {
            const value = getValue();
            if (isVisible() && !isEmpty(value) && (isNaN(Number(value)) || (typeof Number(value) !== 'number'))) {
                setError(error);
                return false;
            }
            return true;
        }
    },
    email (getValue, setError, isVisible) {
        const error = 'This value must be an email.';
        return function () {
            const value = getValue();
            if (isVisible() && !isEmpty(value) && !/\S+@\S+\.\S+/.test(value)) {
                setError(error);
                return false;
            }
            return true;
        }
    },
    url (getValue, setError, isVisible) {
        const error = 'This value must be a valid url.';
        return function () {
            const value = getValue();
            if (isVisible() && !isEmpty(value) && !isUrl(value)) {
                setError(error);
                return false;
            }
            return true;
        }
    },

    vueForm (getFormInstance, setError, isVisible) {
        return function () {
            if (isVisible()) {
                return getFormInstance().$refs.vForm.validate();
            } else {
                return false;
            }
        }
    }
}
export const registerValidation = function (validation, getValue, setError, isVisible) {
    registeredValidations.native.push(
        validationFactory[validation](getValue, setError, isVisible)
    );
}

export const runValidations = function () {
    let isValid = true;
    registeredValidations.native.forEach((validation) => {
        const result = validation.call();
        if (!result) {
            isValid = false;
        }
    });

    error(isValid, 'Please correct submission.');
}



