<template lang="html">
    <div> <!-- single root element -->
        <template v-if="goldAnswers">
            <slot name="correct-answers-banner">
                <div style="color:#F39C12" class="h1 text-center text-uppercase jumbotron">
                    <slot name="correct-answers-banner-content">
                        Correct answers
                    </slot>
                </div>
            </slot>
        </template>
        <template v-else-if="outOfGoldenTasks">
            <slot name="out-of-quiz-questions-banner">
                <div class="jumbotron">
                    <p>
                        <slot name="out-of-quiz-questions-banner-content">
                            We have run out of quiz questions for you. Please notify the project owner.
                        </slot>
                    </p>
                </div>
            </slot>
        </template>
        <template v-else-if="failedQuiz">
            <slot name="failed-quiz-banner" v-bind="slotParams">
                <div class="jumbotron">
                    <p>
                        <slot name="failed-quiz-banner-content" v-bind="slotParams">
                            Thank you for taking the quiz. You got {{ slotParams.right }} correct out of {{ slotParams.total }} tasks. You have been blocked from working on this job. The administrator of this job will contact you with next steps.
                        </slot>
                    </p>
                </div>
            </slot>
        </template>
        <template v-else-if="passedQuiz">
            <slot name="passed-quiz-banner" v-bind="slotParams" :startJob="clearQuizPassedAlert">
                <div class="jumbotron">
                    <p>
                        <slot name="passed-quiz-banner-content" v-bind="slotParams">
                            Thank you for taking the quiz. You got {{ slotParams.right }} correct out of {{ slotParams.total }} tasks. You will now be able to work on this job.
                        </slot>
                    </p>
                    <p><button class="btn-primary" @click="clearQuizPassedAlert">Start Job</button></p>
                </div>
            </slot>
        </template>
        <template v-else-if="projectCompleted">
            <slot name="project-completed-banner">
                <div class="jumbotron">
                    <p>
                        <slot name="project-completed-banner-content">
                            Congratulations, you have completed the job.
                        </slot>
                    </p>
                </div>
            </slot>
        </template>
        <template v-else-if="quizStarted">
            <slot name="quiz-started-banner" :startQuiz="clearAlerts">
                <div class="jumbotron">
                    <p>
                        <slot name="quiz-started-banner-content">
                            You must complete a quiz successfully before you can work on this job.
                        </slot>
                    </p>
                    <p><button class="btn-primary" @click="clearAlerts">Start Quiz</button></p>
                </div>
            </slot>
        </template>
        <template v-else-if="inQuizMode">
            <slot name="quiz-mode-banner">
                <div class="h1 text-center text-uppercase jumbotron">
                    <slot name="quiz-mode-banner-content">
                        In quiz mode
                    </slot>
                </div>
            </slot>
        </template>
        <template v-else-if="inGoldMode">
            <slot name="gold-mode-banner">
                <div style="color:#F39C12" class="h1 text-center text-uppercase jumbotron">
                    <slot name="gold-mode-banner-content">
                        In gold mode
                    </slot>
                </div>
            </slot>
        </template>
    </div>
</template>

<script>
import { CLEAR_ALERTS } from '../../store/mutations'
import { mapModuleState, mapModuleMutations, mapModuleGetters } from '../../store/util'
import { getNamespace } from '../../bus'
import { STATUS, QUIZ } from '../../store/getters'

export default {
    data () {
        return {};
    },

    computed: {
        slotParams () {
            const { right, wrong } = this.quiz.result;
            return {
                right,
                wrong,
                total: right + wrong
            };
        },

        inGoldMode () {
            return this.status.inGoldMode;
        },

        inQuizMode () {
            return this.status.quizInProgress;
        },

        quizStarted () {
            return this.alerts.quizStarted;
        },

        outOfGoldenTasks () {
            return this.alerts.outOfGoldenTasks;
        },

        failedQuiz () {
            return this.alerts.quizFailed;
        },

        passedQuiz () {
            return this.alerts.quizPassed;
        },

        projectCompleted () {
            return this.alerts.projectCompleted;
        },

        ...mapModuleState(
            getNamespace,
            [
                'alerts',
                'goldAnswers'
            ]
        ),

        ...mapModuleGetters(
            getNamespace,
            {
                status: STATUS,
                quiz: QUIZ
            }
        )
    },

    methods: {
        ...mapModuleMutations(
            getNamespace,
            {
                _clearAlerts: CLEAR_ALERTS
            }
        ),

        clearAlerts () {
            this._clearAlerts();
        },

        clearQuizPassedAlert () {
            this._clearAlerts('quizPassed');
        }

    }
}
</script>

<style lang="css" scoped>
</style>



