<template lang="html">
    <span v-if="!isTagged" class="display-segment">{{ displaySegment.text }}</span>
    <span v-else-if="isTagged"><span v-for="(w, i) in words" :key="i" class="display-segment bracket tagged-text"
                                     :data-num-segments="numSegments" @click="edit($event)">{{ w.word }}<span
        v-for="(b, j) in w.brackets" :key="j" :data-segment-index="b.segmentIndex" class="bracket"
        :class="{ 'head': b.head, 'tail': b.tail }" :style="b.style"></span></span></span>
</template>

<script>
import utils from './utils.js'

export default {
    props: {
        displaySegment: Object,
        segments: Array,
        tags: Object
    },

    methods: {
        edit (event) {
            if (this.isTagged) {
                this.$emit('edit', event);
            }
        }
    },
    computed: {
        isTagged () {
            return this.numSegments > 0;
        },
        numSegments () {
            return this.displaySegment['segmentIndices'].length;
        },
        words () {
            return utils.splitIntoWordsAndWhitespaces(this.displaySegment.text).map(wordRange => {
                return {
                    word: wordRange.word,
                    brackets: this.displaySegment['segmentIndices'].map(j => {
                        const segment = this.segments[j];
                        const color = segment.tags.size > 1 ? 'black' : this.tags[segment.tags.values().next().value].color;
                        const head = segment.offset === this.displaySegment.start + wordRange.start; // First display segment for segment
                        const tail = segment.endOffset === this.displaySegment.start + wordRange.end; // Last display segment for segment
                        return {
                            segmentIndex: j,
                            head: head,
                            tail: tail,
                            style: {
                                'border-color': color
                            }
                        };
                    })
                };
            });
        }
    }
}
</script>

<style lang="css" scoped>
.display-segment.bracket.tagged-text {
    position: relative;
}

.display-segment.bracket.tagged-text > .bracket {
    position: absolute;
    border: 2px none;
    border-bottom-style: solid;
    left: 0;
    top: 1em;
    width: 100%;
    height: 1em;
    text-align: center;
    /* Exclude from highlighting*/
    -moz-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.display-segment.bracket.tagged-text > .bracket.head {
    border-left: 2px solid;
}

.display-segment.bracket.tagged-text > .bracket.tail {
    border-right: 2px solid;
}
</style>



