'use strict'
import { registerValidation } from '../validation.js'

const validatedElement = {
    props: ['validations'],
    mounted () {
        const cleanAndValidations = this.validations ? ['clean', ...this.validations] : []
        cleanAndValidations.forEach(validation => {
            registerValidation(validation,
                () => this.currentValue,
                this.setError.bind(this),
                () => this.isVisible);
        });
    },
    created () {
        this.isVisible = true;
    },
    destroyed () {
        this.isVisible = false;
    },
    data () {
        return {
            error: null,
            isVisible: true
        };
    },
    methods: {
        setError (error) {
            this.error = error;
        }
    }
}
export default validatedElement;



