<template>
    <vue-form-generator ref="vForm" @model-updated="updated" :options='options' :schema="schema" :model="model"></vue-form-generator>
</template>
<script>
import VueFormGenerator from 'vue-form-generator';
import { inputBus, getNamespace } from '../../bus'
import validatedElement from '../../mixins/validatedElement.js';
import { mapModuleState, getSingleAnswer } from '../../store/util'
export default {
    components: {
        VueFormGenerator: VueFormGenerator.component
    },
    mixins: [validatedElement],

    data () {
        return {
            model: {...this.defaultValue}
        }
    },
    computed: {
        currentValue () {
            /* For this component we use the instance of vue-generator-form.
               The validator func will use this instance to determine if is valid or not.
            */
            return this;
        },
        ...mapModuleState(
            getNamespace,
            [
                'taskId',
                'answers'
            ]
        )
    },
    props: {
        name: { type: String, required: false },
        options: { type: Object },
        schema: { type: Object },
        defaultValue: { type: Object, default: () => {} },
        pybAnswer: {type: String, required: true},
        validations: {type: Array, default: () => ['vueForm']}
    },
    updated () {
        const value = getSingleAnswer(this.answers, this.pybAnswer) || this.defaultValue;
        inputBus.$emit('input', {
            name: this.pybAnswer, value
        });
    },
    methods: {
        updated (value, schema) {
            const name = `${this.pybAnswer}.${schema}`;
            inputBus.$emit('input', {
                name, value
            });
        }
    },
    watch: {
        taskId () {
            this.model = {...this.defaultValue};
        },
        answers (newVal) {
            if (newVal) {
                this.model = getSingleAnswer(newVal, this.pybAnswer);
            }
        }
    }
}
</script>



