<template lang="html">
    <span class="last-save-time">
        <span v-if="lastSaveTime">
            Answers last saved at: {{lastSaveTime.toLocaleString()}}
        </span>
    </span>
</template>

<script>
import { mapModuleState } from '../../store/util'
import { getNamespace } from '../../bus'

export default {
    data () {
        return {
            showInstructions: true
        }
    },

    computed: {
        ...mapModuleState(
            getNamespace,
            [
                'lastSaveTime'
            ]
        )
    }
}
</script>



