import lodashFlow from 'lodash.flow';
import lodashTrimEnd from 'lodash.trimend';
import lodashGet from 'lodash.get';
import lodashSet from 'lodash.set';

export const ID_COL_NAME = '__col-id';

export const mapModuleState = lodashFlow(getModuleStateMappers, toObject);

export const mapModuleMutations = lodashFlow(getModuleMutations, toObject);

export const mapModuleGetters = lodashFlow(getModuleGetters, toObject);

export const mapModuleActions = lodashFlow(getModuleActions, toObject);

function constructTablePath (tableName, rowId, answerPath) {
    return `${tableName}[${rowId}].${answerPath}`;
}

export function getTableAnswer (answers, tableName, idColName, rowId, answerPath) {
    const index = answers[tableName].findIndex(item => item[idColName] === rowId)
    return lodashGet(answers, constructTablePath(tableName, index, answerPath));
}

export function setTableAnswer (answers, tableName, idColName, rowId, answerPath, answer) {
    // first time adding a bind value to table
    if (!answers[tableName]) {
        answers[tableName] = [];
    }
    // if element is not there push it with the id
    let index = answers[tableName].findIndex(item => item[idColName] === rowId)
    if (index === -1) {
        answers[tableName].push({[idColName]: rowId})
        index = answers[tableName].length - 1;
    }
    const jsonPath = constructTablePath(tableName, index, answerPath);
    return { answers: setSingleAnswer(answers, jsonPath, answer), path: jsonPath };
}

export function getSingleAnswer (answers, answerPath) {
    return lodashGet(answers, answerPath);
}

export function setSingleAnswer (answers, answerPath, answer) {
    lodashSet(answers, answerPath, answer)
    return Object.assign({}, answers);
}

// Return the moduleName without trailing /.
// Return '' if no moduleName.
function getNormalizedModuleName (vm, getModuleNameFn) {
    const moduleName = getModuleNameFn(vm);
    if (!moduleName) return '';
    return lodashTrimEnd(moduleName, '/');
}

function prefixModuleName (vm, getModuleNameFn, name) {
    const moduleName = getNormalizedModuleName(vm, getModuleNameFn);
    if (!moduleName) return name;
    return `${moduleName}/${name}`;
}

function* getModuleActions (getModuleNameFn, mappers) {
    for (const [localName, actionName] of normalizeEntries(mappers)) {
        const method = function (payload) {
            return this.$store.dispatch(
                prefixModuleName(this, getModuleNameFn, actionName),
                payload
            );
        };
        yield [localName, method];
    }
}

function* getModuleGetters (getModuleNameFn, mappers) {
    for (const [localName, getterName] of normalizeEntries(mappers)) {
        const getter = function () {
            return this.$store.getters[prefixModuleName(this, getModuleNameFn, getterName)];
        };
        yield [localName, getter];
    }
}

function* getModuleMutations (getModuleNameFn, mappers) {
    for (const [localName, mutationName] of normalizeEntries(mappers)) {
        const method = function (payload) {
            this.$store.commit(
                prefixModuleName(this, getModuleNameFn, mutationName),
                payload
            );
        };
        yield [localName, method];
    }
}

function* getModuleStateMappers (getModuleNameFn, mappers) {
    for (const [name, mapper] of normalizeStateMappers(mappers)) {
        const moduleMapper = function () {
            const moduleName = getNormalizedModuleName(this, getModuleNameFn);
            const rootState = this.$store.state;
            const moduleState = moduleName ? lodashGet(rootState, moduleName.split('/')) : rootState;
            return mapper(moduleState);
        };
        yield [ name, moduleMapper ];
    }
}

function toObject (keyValueIterator) {
    const result = {};
    for (const [key, value] of keyValueIterator) {
        result[key] = value;
    }
    return result;
}

// Returns array of key,value pairs.
// For array, key = value.
function normalizeEntries (arrayOrObject) {
    if (Array.isArray(arrayOrObject)) {
        return arrayOrObject.map(x => [x, x]);
    } else {
        return Object.entries(arrayOrObject);
    }
}

function* normalizeStateMappers (mappers) {
    for (const [localName, pathOrGetter] of normalizeEntries(mappers)) {
        yield [localName, getStateGetter(pathOrGetter)];
    }
}

function getStateGetter (pathOrGetter) {
    if (typeof pathOrGetter === 'string') {
        return (state) => lodashGet(state, pathOrGetter);
    } else {
        return pathOrGetter;
    }
}



