<template lang="html">
    <transition name="fade">
        <div class="row alert-banner" v-if="message && message.text">
            <div class="col-md-12">
                <div class="alert" :class="type">
                    <span class="close" @click="hide">&times;</span>
                    <span class="message" v-html="message.text"></span>
                </div>
            </div>
        </div>
    </transition>
</template>

<script>
import { SET_MESSAGE } from '../../store/mutations.js'
import { mapModuleState, mapModuleMutations } from '../../store/util'
import { getNamespace } from '../../bus'

export default {
    data () {
        return {
            visible: true,
            timeout: null
        }
    },

    methods: {
        hide () {
            this[SET_MESSAGE]({});
        },

        ...mapModuleMutations(
            getNamespace,
            [
                SET_MESSAGE
            ]
        )
    },

    computed: {
        ...mapModuleState(
            getNamespace,
            [
                'message'
            ]
        ),

        type () {
            const type = this.message.type || 'alert-success';
            return { [type]: true };
        }
    },

    watch: {
        message (newVal) {
            if (!newVal) return;
            if (this.timeout) {
                window.clearTimeout(this.timeout);
                this.timeout = null;
            }
            if (newVal.duration) {
                this.timeout = window.setTimeout(() => {
                    this.hide();
                }, this.message.duration);
            }
        }
    }
}
</script>

<style lang="css" scoped>
.alert-banner {
    position: absolute;
    z-index: 5;
    right: 3em;
}

.message {
    padding: 0 1em;
    display: inline-block;
}

.fade-enter-active, .fade-leave-active {
    transition: opacity .5s;
}

.fade-enter, .fade-leave-to {
    opacity: 0;
}
</style>



