'use strict'
import { mapModuleState, getSingleAnswer, getTableAnswer, ID_COL_NAME } from '../store/util'
import { inputBus, getNamespace } from '../bus'

function setId (element) {
    // Move the id to the input element
    const id = element.getAttribute('id');
    if (id) {
        element.removeAttribute('id');
        element.firstElementChild.setAttribute('id', id);
    }
}

function setBind (vm) {
    const name = vm.pybAnswer
    const tableAnswerName = vm.pybTableAnswer;
    if (name) {
        bindAnswer(vm, name);
    } else if (tableAnswerName && vm.$parent.$vnode) {
        const tableName = vm.$parent.$vnode.data.ref;
        const idColName = vm.$parent.$vnode.context.columnId;
        bindTableAnswer(vm, idColName, tableAnswerName, tableName);
    }
}

function bindTableAnswer (vm, idColName, colName, tableName) {
    vm.$on('input', (value) => {
        const rowId = vm.row[idColName];
        inputBus.$emit('table-input', {
            tableName, idColName, colName, rowId, value
        });
    });
}

function bindAnswer (vm, name) {
    vm.$on('input', (value) => {
        inputBus.$emit('input', {
            name, value
        });
    });
}

const inputElement = {
    props: {
        initialValue: {
            default: null
        },
        row: null,
        pybAnswer: String,
        pybTableAnswer: String
    },

    mounted () {
        setId(this.$el);
        setBind(this);
        this._resetValue();
    },
    data () {
        return {
            _currentValue: null
        };
    },

    methods: {
        updated (newVal) {
            this._setValue(newVal);
        },

        _setValue (newVal) {
            this.currentValue = newVal === '' ? null : newVal;
            this.$emit('input', this.currentValue);
        },

        _resetValue () {
            this._setValue(this.initialValue);
        }
    },

    computed: {
        currentValue: {
            get () {
                return this.$data._currentValue;
            },

            set (newValue) {
                this.$data._currentValue = newValue;
            }
        },

        ...mapModuleState(
            getNamespace,
            [
                'active',
                'taskId',
                'answers'
            ]
        )
    },

    watch: {
        taskId () {
            this._resetValue();
        },

        answers (newVal) {
            let val = this.initialValue;
            if (newVal) {
                if (this.pybAnswer) {
                    val = getSingleAnswer(newVal, this.pybAnswer);
                } else if (this.pybTableAnswer) {
                    const tableName = this.$parent.$vnode.data.ref;
                    const idColName = this.$parent.$vnode.context.columnId;
                    const rowId = this.row[ID_COL_NAME]
                    val = getTableAnswer(newVal, tableName, idColName, rowId, this.pybTableAnswer);
                }
            }
            this.$data._currentValue = val;
        }
    }
}

export default inputElement;



