// Define hooks
const hooks = {
    setUp (config) {
        return config;
    },

    beforePresent (task, config) {
        return Promise.resolve(task);
    },

    beforeSubmit (rawAns, task, config) {
        return Promise.resolve(rawAns);
    }
};

function setupHooks (pybossa) {
    pybossa.hooks = {};
    for (const key in hooks) {
        // set default value
        pybossa.hooks[key] = hooks[key];

        // setter functions
        pybossa[key] = function (hook) {
            pybossa.hooks[key] = hook;
        };
    }
};

export default setupHooks;



