<template lang="html">
    <button :class="btnClass"
        :disabled="!active">
        <slot>Submit</slot>
    </button>
</template>

<script>
import { mapModuleState } from '../../store/util'
import { getNamespace } from '../../bus'

export default {
    props: {
        btnClass: String
    },

    computed: {
        ...mapModuleState(
            getNamespace,
            [
                'active'
            ]
        )
    }
}
</script>



