<template lang="html">
    <div v-if="url.link" class="link-url">
        <a class = "link" :href="url.link" target="__blank" >
            <slot class = "link-name"></slot>
        </a>
    </div>
</template>

<script>
export default {
    props: {
        url: {link: ''}
    }
}
</script>



