<script>
import components from '../../components.js'
import { inputBus } from '../../bus'

import Vue from 'vue'
import { ClientTable } from 'vue-tables-2';
Vue.use(ClientTable, { });
const MAX_ELEMENT_IN_PAGE = 1000000;
const COLUMN_ID = '__col-id';

export default {
    components,
    data () {
        return {updateKey: 'load', counter: 0, internalData: []}
    },
    props: {
        name: { type: String, required: true },
        data: { type: Array },
        columns: { type: Array, required: true },
        options: { type: Object },
        columnId: {type: String, default: COLUMN_ID},
        rowObject: { type: Object, default: () => { return {} } },
        enableAddRows: { type: Boolean, default: false },
        addButtonAfterTable: { type: Boolean, default: true },
        addButtonBeforeTable: { type: Boolean, default: false }
    },
    created () {
        this.initialize()
    },
    mounted () {
        inputBus.$on('table-input', ({tableName, idColName, rowId, colName, value: answer}) => {
            this.internalData = this.internalData.map((e) => {
                if (e[idColName] === rowId && this.name === tableName) {
                    e[colName] = answer;
                }
                return e;
            })
        });
    },
    computed: {
        customOptions: function () {
            let customOptions = {}
            customOptions = this.options ? this.options : {};
            customOptions.perPage = MAX_ELEMENT_IN_PAGE;
            customOptions.perPageValues = [MAX_ELEMENT_IN_PAGE];
            customOptions.sortIcon = {
                base: 'fa',
                up: 'fa-sort-up',
                down: 'fa-sort-up',
                is: 'fa-sort'
            }
            customOptions.filterByColumn = false
            customOptions.filterable = []
            customOptions.sortable = []

            customOptions.texts = { filter: '', count: '' }
            if (this.enableAddRows) {
                customOptions.headings = { ...customOptions.headings, __delete__: 'Delete' }
            }
            return customOptions;
        },
        customColumns: function () {
            const cols = this.columns;
            if (this.enableAddRows) {
                cols.push('__delete__');
            }
            return cols;
        }
    },
    methods: {
        initialize: function () {
            if (this.data) {
                for (let i = 0; i < this.data.length; i++) {
                    if (!this.data[i].hasOwnProperty(this.columnId) || this.addRemoveButtons) {
                        this.internalData[i] = {...this.data[i], [this.columnId]: this.counter}
                    } else {
                        this.internalData[i] = {...this.data[i]};
                    }
                    this.counter++;
                }
            }
        },
        checkIdExist: function (newId) {
            let exist = false;
            // Only check if they pick a special ID column
            if (this.columnId !== COLUMN_ID) {
                this.internalData.forEach((e) => {
                    if (e[this.columnId] === newId) {
                        exist = true;
                    }
                })
            }
            return exist;
        }
    },
    render (h) {
        const addrow = () => {
            while (this.checkIdExist(this.counter)) { this.counter++; };
            const obj = {[this.columnId]: this.counter, ...this.rowObject}
            const tableName = this.name;
            this.internalData.push(obj);
            inputBus.$emit('add-table-input', {
                tableName, obj
            });
        };

        const delrow = (row) => {
            this.updateKey = Math.random();
            const rowId = row[this.columnId];
            this.internalData = this.internalData.filter((e) => e[this.columnId] !== rowId)
            const tableName = this.name;
            inputBus.$emit('delete-table-input', {
                tableName, idColName: this.columnId, rowId
            });
        }

        const addRowButton = this.enableAddRows ? h('div', {staticClass: 'row form-group'},
            [h('div', {staticClass: 'col-md-12'}, [h('button', {staticClass: 'btn btn-info pull-right',
                attrs: {'id': 'add_row'},
                on: {'click': function ($event) {
                    return addrow();
                }}},
            [h('span', 'Add')])
            ], 1)]) : '';

        const deleteRowButton = function (props) {
            return h('div', {staticClass: 'form-group'},
                [h('button', {staticClass: 'btn btn-danger',
                    attrs: {'id': 'delete_row', 'slot': '__delete__'},
                    on: {'click': ($event) => { return delrow(props.row) }}},
                [h('span', 'X')])], 1);
        }

        const getScopes = () => {
            const normalizedSlots = (this.$vnode.data.scopedSlots && this.$vnode.data.scopedSlots._normalized) ? this.$vnode.data.scopedSlots._normalized : this.$vnode.data.scopedSlots;
            this.$vnode.data.scopedSlots = { ...normalizedSlots,
                ...(this.enableAddRows ? {__delete__: (props) => {
                    return deleteRowButton(props);
                }} : {})
            }
            return this.$vnode.data.scopedSlots;
        }

        return h('v-client-table', {
            ref: this.name,
            key: this.updateKey,
            props: {
                columns: this.customColumns,
                options: this.customOptions,
                data: this.internalData
            },
            attrs: {
                id: this.name
            },
            scopedSlots:
            { ...getScopes() }
        }, [this.addButtonBeforeTable ? h(
            'template', {
                staticClass: 'row',
                slot: 'beforeTable'},
            [addRowButton]) : '', this.addButtonAfterTable ? h(
            'template', {
                staticClass: 'row',
                slot: 'afterTable'},
            [addRowButton]) : '']);
    }
}
</script>



