<template lang="html">
    <div class="file-upload">
        <input v-if="uploadReady"
             :disabled="!active"
             type="file"
             @change="onFileChange">
    </div>
</template>

<script>
import inputElement from '../../mixins/inputElement.js'

export default {
    mixins: [inputElement],
    data () {
        return {uploadReady: true}
    },
    props: {
        fileName: ''
    },
    methods: {
        onFileChange (e) {
            const files = e.target.files;
            if (files.length && files[0]) {
                const name = this.fileName ? this.getFilename(files[0].name) : files[0].name;
                this.updated({file: files[0], name})
            }
        },
        getExtension (name) {
            var parts = name.split('.');
            if (parts.length > 1) {
                return parts[parts.length - 1];
            }
        },
        getFilename (currentName) {
            const ext = this.getExtension(currentName);
            return `${this.fileName}${ext ? ('.' + ext) : ''}`
        },
        clear () {
            this.uploadReady = false
            this.$nextTick(() => {
                this.uploadReady = true
            })
        }
    },

    watch: {
        currentValue (newValue) {
            if (!newValue) {
                this.clear();
            }
        }
    }
}
</script>



