<template lang="html">
    <div class="task-timer" v-if="taskId">
        <div v-if="remainingSeconds > 0">
            <p>
                <span class="lock-time" v-if="lock && lock.toLowerCase() === 'true'">Locked on {{timeStamp.toLocaleString()}}<br/></span>
                Time Remaining:
                <span v-if="hours">{{pluralize(hours, 'hour')}},</span>
                <span v-if="hours||minutes">{{pluralize(minutes, 'minute')}},</span>
                {{pluralize(seconds, 'second')}}
            </p>
        </div>
        <div v-else>
            <p>Task expired</p>
        </div>
    </div>
</template>

<script>
import { mapModuleState } from '../../store/util'
import { getNamespace } from '../../bus'

const MILLISECONDS_PER_SECOND = 1000;
const SECONDS_PER_MINUTE = 60;
const MINUTES_PER_HOUR = 60;
const SECONDS_PER_HOUR = SECONDS_PER_MINUTE * MINUTES_PER_HOUR;

export default {
    props: {
        lock: false
    },

    data () {
        return {
            interval: null,
            remainingSeconds: 0
        };
    },

    mounted () {
        this.resetTimer();
    },

    methods: {
        pluralize (count, text) {
            return `${count} ${text}${count === 1 ? '' : 's'}`;
        },

        resetTimer () {
            this.updateRemaining();
            this.interval && window.clearInterval(this.interval);
            this.interval = window.setInterval(
                this.updateRemaining.bind(this),
                Math.min(MILLISECONDS_PER_SECOND, this.remainingSeconds * MILLISECONDS_PER_SECOND)
            );
        },

        updateRemaining () {
            this.remainingSeconds = this.expiration - Date.now() / MILLISECONDS_PER_SECOND;
        }
    },

    computed: {
        ...mapModuleState(
            getNamespace,
            [
                'taskId'
            ]
        ),

        ...mapModuleState(
            getNamespace,
            {
                expiration: 'expiration.seconds',
                lockTime: 'expiration.lockTime'
            }
        ),

        hours () {
            return Math.floor(this.remainingSeconds / SECONDS_PER_HOUR);
        },

        minutes () {
            return Math.floor((this.remainingSeconds % SECONDS_PER_HOUR) / MINUTES_PER_HOUR);
        },

        seconds () {
            return Math.floor(this.remainingSeconds % SECONDS_PER_MINUTE);
        },

        timeStamp () {
            const date = new Date(this.lockTime);
            return new Date(date - date.getTimezoneOffset() * 60 * 1000);
        }
    },

    watch: {
        expiration () {
            this.resetTimer();
        }
    }
}
</script>



