<template lang="html">
    <div class="radio-input">
        <input type="radio"
               :name="name"
               :value="value"
               :checked="checked"
               :disabled="!active"
               @change="input">
    </div>
</template>

<script>
import inputElement from '../../mixins/inputElement.js';

export default {
    mixins: [inputElement],

    props: {
        name: null,
        initialValue: {
            default: null
        },
        value: null
    },

    data () {
        return {};
    },

    methods: {
        input () {
            // Change event only happens when checked, not when unchecked.
            this._setValue(this.value);
        },

        _setValue (newVal) {
            this.currentValue = newVal;

            if (this.checked) {
                this.$emit('input', this.value);
            }
        }
    },

    computed: {
        checked () {
            return !!this.currentValue && (this.currentValue === this.value);
        }
    }
}
</script>



