<template lang="html">
    <div class='user-progress'>
        <template v-if="quizInProgress">
            <p>You are in quiz mode.</p>
            <p>You are working now on task # <span class="label label-warning">{{taskId}}</span></p>
            <p>You have answered {{pluralize(right, 'question')}} correctly.</p>
            <p>You have answered {{pluralize(wrong, 'question')}} incorrectly.</p>
            <p v-if="isFailed">You have failed the quiz, so you are blocked from the project.</p>
            <p>There {{pluralize(questionsRemaining, 'is', { showCount: false, plural: 'are' })}} {{pluralize(questionsRemaining, 'question')}} remaining.</p>
            <div class="progress progress-striped">
                <div rel="tooltip" title="#" class="progress-bar progress-bar-success" :style="quizAnsweredBarWidth"></div>
            </div>
        </template>
        <template v-else>
            <p>You are working now on task # <span class="label label-warning">{{taskId}}</span></p>
            <p>You have completed: <span class="label label-info">{{done}}</span> {{pluralize(done, 'task', {showCount:false})}} from
            <span class="label label-info">{{total}}</span></p>
            <p>Number of tasks remaining: <span class="label label-info">{{remainingForUser}}</span></p>
            <template v-if="status.inGoldMode">
                <p>Total number of available gold tasks: <span class="label label-warning">{{availableGoldTasks}}</span></p>
                <p>Percentage of gold tasks: <span class="label label-warning">{{ goldTaskPercentage.toFixed(2) }}%</span></p>
            </template>
            <div class="progress progress-striped">
                <div rel="tooltip" title="#" class="progress-bar progress-bar-success" :style="barWidth"></div>
            </div>
        </template>
    </div>
</template>

<script>
import { mapModuleState, mapModuleGetters } from '../store/util'
import { getNamespace } from '../bus'
import { STATUS, QUIZ } from '../store/getters'

export default {
    data () {
        return {};
    },

    computed: {
        quizInProgress () {
            return this.status.quizInProgress
        },

        percentage () {
            return 100 * this.done / (this.total || 1);
        },

        barWidth () {
            return `width: ${this.percentage}%`
        },

        quizAnsweredPercent () {
            return 100 * (this.right + this.wrong) / this.totalQuestions;
        },

        quizAnsweredBarWidth () {
            return `width: ${this.quizAnsweredPercent}%`;
        },

        questionsRemaining () {
            return this.totalQuestions - this.right - this.wrong;
        },

        isFailed () {
            return this.status.quizFailed;
        },

        right () {
            return this.quiz.result.right;
        },

        wrong () {
            return this.quiz.result.wrong;
        },

        totalQuestions () {
            return this.quiz.config.questions;
        },

        goldTaskPercentage () {
            return (this.availableGoldTasks || 0) * 100 / this.total;
        },

        ...mapModuleGetters(
            getNamespace,
            {
                status: STATUS,
                quiz: QUIZ
            }
        ),

        ...mapModuleState(
            getNamespace,
            [
                'taskId',
                'projectName'
            ]
        ),

        ...mapModuleState(
            getNamespace,
            {
                remainingForUser: 'progress.remaining_for_user',
                remaining: 'progress.remaining',
                total: 'progress.total',
                done: 'progress.done',
                availableGoldTasks: 'progress.available_gold_tasks'
            }
        )
    },

    methods: {
        pluralize (count, text, { showCount = true, plural = text + 's' } = {}) {
            return `${showCount ? count + ' ' : ''}${count === 1 ? text : plural}`;
        }
    }
}
</script>

<style lang="css" scoped>
</style>



