const utils = {

    splitIntoWordsAndWhitespaces (text) {
        // Not comprehensive... There are prolly more
        const whitespaces = new Set([' ', '\t', '\n']);
        let wordInMaking = [];
        let wordStart = 0;
        const wordRanges = new Array(0);
        for (let i = 0; i < text.length; i++) {
            const char = text.charAt(i);
            if (whitespaces.has(char)) {
                if (wordInMaking.length > 0) {
                    wordRanges.push({word: wordInMaking.join(''), start: wordStart, end: i});
                }
                wordRanges.push({word: char, start: i, end: i + 1});
                wordInMaking = [];
                wordStart = i + 1;
            } else {
                wordInMaking.push(char);
            }
        }
        if (wordInMaking.length > 0) {
            wordRanges.push({word: wordInMaking.join(''), start: wordStart, end: text.length});
        }
        return wordRanges;
    },

    /** Create display segments, where all overlapping segments are broken into smaller segments.
     For example, if "foo bar baz" has two segments; [("foo bar"), ("bar baz")], split into 3 display segments,
     and record with each display segment the indices of the original segments they cover
     [("foo", [0]), (" bar ", [0, 1]), ("baz", [2])]
     */
    splitAndIndex (text, segments) {
        const displaySegments = new Array(0);
        const forwardIndex = this.defaultMap(); // Maps start offset => end offset => segment index
        const backwardIndex = this.defaultMap(); // Maps end offset => start offset => segment index
        const offsets = Array.from(new Set(
            // FIXME - Replace with flatMap() once on a higher ECMA ver.
            segments
                .map((s, sIndex) => {
                    forwardIndex.putIfAbsent(s.offset, {})[s.endOffset] = sIndex;
                    backwardIndex.putIfAbsent(s.endOffset, {})[s.offset] = sIndex;
                    return [s.offset, s.endOffset];
                })
                .reduce((a1, a2) => a1.concat(a2), [0, text.length])
        ));
        offsets.sort((o1, o2) => o1 < o2 ? -1 : (o1 === o2 ? 0 : 1));

        let openSegmentIndices = [];
        let prevSegmentOffset = -1;
        for (const offset of offsets) {
            if (prevSegmentOffset > -1) {
                displaySegments.push({
                    text: text.substring(prevSegmentOffset, offset),
                    start: prevSegmentOffset,
                    end: offset,
                    segmentIndices: openSegmentIndices
                });
            }
            // Remove segments that end at this offset
            openSegmentIndices = openSegmentIndices.filter(sIndex => sIndex !== backwardIndex.getOrDefault(offset, {})[segments[sIndex].offset]);
            // Add segments that start at this offset
            Object.values(forwardIndex.getOrDefault(offset, {})).forEach(sIndex => openSegmentIndices.push(sIndex));

            prevSegmentOffset = offset;
        }

        return {segments, displaySegments, forwardIndex, backwardIndex};
    },

    defaultMap () {
        const DefaultMap = function () {
            Map.call(this)
        };
        DefaultMap.prototype = Object.create(Map.prototype);
        DefaultMap.prototype.constructor = DefaultMap;
        DefaultMap.prototype.map = function (key, func) {
            const currentVal = this.get(key);
            const newVal = func(currentVal);
            this.set(key, newVal);
        };
        DefaultMap.prototype.putIfAbsent = function (key, val) {
            const currentVal = this.get(key);
            if (currentVal === undefined) {
                this.set(key, val);
                return val;
            } else {
                return currentVal;
            }
        };
        DefaultMap.prototype.getOrDefault = function (key, val) {
            const currentVal = this.get(key);
            if (currentVal === undefined) {
                return val;
            } else {
                return currentVal;
            }
        }
        return new DefaultMap();
    }

};

export default utils;



