<template lang="html">
    <span></span>
</template>

<script>
import { mapModuleState } from '../../store/util'
import { getNamespace, inputBus } from '../../bus'

const MILLISECONDS_PER_SECOND = 1000;

export default {
    props: {
        // save interval in seconds
        saveInterval: {
            type: Number,
            default: 60
        },
        allowAssignToUser: {
            type: Boolean,
            default: false
        }
    },

    data () {
        return {
            interval: null
        };
    },

    mounted () {
        this.resetTimer();
    },

    methods: {
        resetTimer () {
            this.interval && window.clearInterval(this.interval);
            this.interval = window.setInterval(
                () => {
                    inputBus.$emit('save-answers');
                    if (this.allowAssignToUser) {
                        inputBus.$emit('assign-user');
                    }
                },
                this.intervalInMilliseconds
            );
        }
    },

    computed: {
        intervalInMilliseconds () {
            return this.saveInterval * MILLISECONDS_PER_SECOND;
        },

        ...mapModuleState(
            getNamespace,
            [
                'taskId'
            ]
        )
    },

    watch: {
        taskId () {
            this.resetTimer();
        }
    }
}
</script>



