<template lang="html">
    <base-button
        class="cancel-button"
        :btnClass="'btn btn-default'"
        @click.native="saveAnswers">
        Save Partial Answers
    </base-button>
</template>

<script>
import BaseButton from './BaseButton.vue';
import { mapModuleState } from '../../store/util'
import { getNamespace, inputBus } from '../../bus'

export default {
    props: {
        allowAssignToUser: {
            type: Boolean,
            default: false
        }
    },

    components: { BaseButton },

    methods: {
        saveAnswers () {
            inputBus.$emit('save-answers');
            if (this.allowAssignToUser) {
                inputBus.$emit('assign-user');
            }
        }
    },

    computed: {
        ...mapModuleState(
            getNamespace,
            [ 'taskId' ]
        )
    }
}
</script>

<style lang="css">
</style>



