<template lang="html">
    <div class='button-row'>
        <submit-button></submit-button>
        <submit-last-button></submit-last-button>
        <cancel-button></cancel-button>
        <save-answers-button v-if="allowSaveWork" :allow-assign-to-user="allowAssignToUser"></save-answers-button>
    </div>
</template>

<script>
import SubmitButton from './buttons/SubmitButton.vue';
import SubmitLastButton from './buttons/SubmitLastButton.vue';
import CancelButton from './buttons/CancelButton.vue';
import SaveAnswersButton from './buttons/SaveAnswersButton.vue';

export default {
    props: {
        allowSaveWork: {
            type: Boolean,
            default: false
        },
        allowAssignToUser: {
            type: Boolean,
            default: false
        }
    },

    components: {
        SubmitButton,
        SubmitLastButton,
        CancelButton,
        SaveAnswersButton
    }
}
</script>



