'use strict'
import Vue from 'vue'
import Vuex from 'vuex'
import * as _mutations from './mutations.js'
import * as _actions from './actions'
import * as _getters from './getters'
import lodashMerge from 'lodash.merge'
import lodashSome from 'lodash.some'

Vue.use(Vuex);

const DEFAULT_STATUS = {
    rightAnswer: false,
    wrongAnswer: false,
    quizFailed: false,
    quizPassed: false,
    quizInProgress: false,
    outOfGoldenTasks: false,
    taskInProgress: false,
    projectCompleted: false,
    isDoomed: false,
    isAssured: false,
    inGoldMode: false
};

export function getInitialState () {
    return {
        vm: undefined,
        projectName: undefined,
        taskId: undefined,
        active: false,
        submitFlag: false,
        leaving: false,
        message: {
            text: '',
            type: undefined
        },
        defaultStatus: DEFAULT_STATUS,
        defaultQuiz: {
            result: {
                right: 0,
                wrong: 0
            },
            status: 'not_started',
            config: {
                enabled: false,
                short_circuit: false,
                questions: 0,
                passing: 0
            }
        },
        _status: undefined,
        progress: {
            done: 0,
            total: 0,
            remaining_for_user: 0,
            remaining: 0,
            quiz: undefined
        },
        alerts: {
            quizStarted: false,
            quizFailed: false,
            quizPassed: false,
            outOfGoldenTasks: false,
            projectCompleted: false
        },
        expiration: {
            seconds: 0,
            lockTime: null
        },
        answers: undefined,
        goldAnswers: false,
        _resolveTask: undefined,
        lastSaveTime: undefined
    };
}

export const mutations = {
    [_mutations.SET_TASK_ID] (state, payload) {
        state.taskId = payload.taskId;
    },

    [_mutations.SET_PROJECT_NAME] (state, payload) {
        state.projectName = payload.projectName;
    },

    [_mutations.SET_ACTIVE] (state) {
        state.active = true;
        state.submitFlag = false;
    },

    [_mutations.SUBMIT] (state, payload) {
        if (payload.last) {
            state.leaving = true;
        }
        state.submitFlag = true;
        state.active = false;
    },

    [_mutations.SET_MESSAGE] (state, payload) {
        state.message = payload;
    },

    [_mutations.SET_STATUS] (state, status) {
        state._status = status;
    },

    [_mutations.SET_PROGRESS] (state, progress) {
        state.progress = progress;
        const quiz = state.progress.quiz;
        // We changed the name of the "pass" property to "passing"
        // so that it is compatible with python where "pass" is a
        // reserved word.
        if (quiz && !('passing' in quiz)) {
            quiz.passing = quiz.pass;
        }
    },

    [_mutations.SET_ALERTS] (state, alerts) {
        state.alerts = alerts;
    },

    [_mutations.CLEAR_ALERTS] (state, alertName) {
        if (alertName) {
            state.alerts[alertName] = false;
        } else {
            state.alerts = {};
        }
    },

    [_mutations.SET_EXPIRATION_SECONDS] (state, {seconds}) {
        state.expiration.seconds = seconds + Date.now() / 1000;
    },

    [_mutations.SET_EXPIRATION_LOCK_TIME] (state, {lockTime}) {
        state.expiration.lockTime = lockTime;
    },

    [_mutations.MERGE_STATE] (state, newState) {
        lodashMerge(state, newState);
    },

    [_mutations.SET_ANSWERS] (state, answers) {
        state.answers = answers;
    },

    [_mutations.SET_GOLD_ANSWERS] (state, goldAnswers) {
        state.goldAnswers = goldAnswers;
    },

    [_mutations.SET_RESOLVE_TASK] (state, resolveTask) {
        state._resolveTask = resolveTask;
    },

    [_mutations.SET_LAST_SAVE_TIME] (state, lastSaveTime) {
        state.lastSaveTime = lastSaveTime;
    }
}

const getters = {
    [_getters.HAS_ALERT] (state) {
        return lodashSome(state.alerts);
    },

    [_getters.STATUS] (state) {
        return state._status || state.defaultStatus;
    },

    [_getters.QUIZ] (state) {
        return state.progress.quiz || state.defaultQuiz;
    }
};

export function getOptions () {
    return {
        state: getInitialState(),
        mutations,
        getters,
        actions: {
            async [_actions.SET_GOLD_ANSWERS] ({commit, state, getters}, goldAnswers) {
                const previousQuizState = state.progress.quiz;
                const progress = await window.pybossa.userProgress(state.projectName);
                commit(_mutations.SET_PROGRESS, progress);
                const newQuiz = getters[_getters.QUIZ];
                const {status, alerts, message} = getNewStatus(state._status, newQuiz, previousQuizState);
                commit(_mutations.SET_STATUS, status);
                commit(_mutations.SET_ALERTS, alerts);
                message && commit(_mutations.SET_MESSAGE, message);
                if (status.wrongAnswer && goldAnswers) {
                    commit(_mutations.SET_ANSWERS, goldAnswers);
                    commit(_mutations.SET_GOLD_ANSWERS, true);
                    return new Promise(function (resolve) {
                        commit(_mutations.SET_RESOLVE_TASK, resolve);
                    });
                }
            },

            [_actions.FINISH_TASK] ({state, commit}) {
                state._resolveTask();
                commit(_mutations.SET_MESSAGE, null);
            },

            async [_actions.NEW_TASK] ({commit, state, getters}, {taskId}) {
                const newQuiz = getters[_getters.QUIZ];

                const quizInProgress = newQuiz.status === 'in_progress';
                const taskInProgress = ['passed', 'not_started'].includes(newQuiz.status);

                const newStatus = {
                    ...state._status,
                    outOfGoldenTasks: !taskId && quizInProgress,
                    projectCompleted: !taskId && taskInProgress
                };

                const newAlerts = {
                    ...state.alerts,
                    outOfGoldenTasks: newStatus.outOfGoldenTasks,
                    projectCompleted: newStatus.projectCompleted
                };
                commit(_mutations.SET_STATUS, newStatus);
                commit(_mutations.SET_ALERTS, newAlerts);
                commit(_mutations.SET_TASK_ID, {taskId});
                commit(_mutations.SET_ANSWERS, null);
                commit(_mutations.SET_GOLD_ANSWERS, false);
                if (!taskId) return;
                const lock = await window.pybossa.fetchLock(taskId);
                commit(_mutations.SET_EXPIRATION_SECONDS, {seconds: lock.expires});
                commit(_mutations.SET_EXPIRATION_LOCK_TIME, {lockTime: lock.lockTime});
            }
        }
    };
}

const store = new Vuex.Store(getOptions());

export default store;

function getStatusForGoldMode () {
    return {
        status: {
            rightAnswer: false,
            wrongAnswer: false,
            quizFailed: false,
            quizPassed: false,
            quizInProgress: false,
            outOfGoldenTasks: false,
            taskInProgress: false,
            projectCompleted: false,
            isDoomed: false,
            isAssured: false,
            inGoldMode: true
        },
        alerts: {
            quizStarted: false,
            quizFailed: false,
            quizPassed: false,
            outOfGoldenTasks: false,
            projectCompleted: false
        },
        message: undefined
    };
}

function getNewStatus (oldStatus, newQuiz, previousQuizState) {
    if (window.pybossa.isGoldMode) return getStatusForGoldMode();

    const quizInProgress = newQuiz.status === 'in_progress';
    const taskInProgress = ['passed', 'not_started'].includes(newQuiz.status);

    const newStatus = {
        ...(oldStatus || DEFAULT_STATUS),
        rightAnswer: previousQuizState && (newQuiz.result.right > previousQuizState.result.right),
        wrongAnswer: previousQuizState && (newQuiz.result.wrong > previousQuizState.result.wrong),
        quizFailed: newQuiz.status === 'failed',
        quizPassed: newQuiz.status === 'passed',
        quizInProgress,
        taskInProgress,
        isDoomed: quizInProgress && (newQuiz.result.wrong > newQuiz.config.questions - newQuiz.config.passing),
        isAssured: quizInProgress && (newQuiz.result.right >= newQuiz.config.passing),
        inGoldMode: false
    };

    oldStatus = oldStatus || newStatus;

    // These alerts require the user's full attention and an acknowldegement before proceeding.
    const alerts = {
        get quizStarted () {
            // If no quiz in progress then nothing to show.
            if (!newStatus.quizInProgress) return false;
            // If status changes from non-quiz to quiz while working in task presenter
            // then give alert. This could happen if the project owner resets the user's
            // quiz results while the user is working.
            if (!oldStatus.quizInProgress) return true;
            // When opening task presenter in quiz mode and no questions have been answered
            // give alert. This could be their first experience with the quiz so we want to make
            // sure they know what is going on.
            // If questions have been answered then there is no need for the alert
            // since they will likely remember that they were taking a quiz, and the banner
            // will remind them.
            if (newQuiz.result.wrong + newQuiz.result.right === 0) return true;
            return false;
        },
        quizFailed: newStatus.quizFailed,
        quizPassed: !oldStatus.quizPassed && newStatus.quizPassed,
        outOfGoldenTasks: newStatus.outOfGoldenTasks,
        projectCompleted: newStatus.projectCompleted
    };

    return {
        status: newStatus,
        alerts,
        message: getMessage()
    };

    // end

    function getMessage () {
        if (newStatus.rightAnswer) {
            return {
                text: 'You got it right',
                type: 'alert-success',
                duration: 5000
            };
        }
        if (newStatus.wrongAnswer) {
            return {
                text: 'You got it wrong',
                type: 'alert-danger',
                duration: 5000
            };
        }
    }
}



