<template lang="html">
    <span
        class="display-segment highlight"
        :class="{ 'tagged-text': !!tag }"
        :style="{ 'color': tag ? tag.color : 'initial', 'background-color': tag ? tag.backgroundColor :'initial' }"
        @click="edit($event)">{{ text }}</span>
</template>

<script>
export default {
    props: {
        text: String,
        tag: Object
    },

    methods: {
        edit (event) {
            if (this.tag) {
                this.$emit('edit', event);
            }
        }
    }
}
</script>



