<template lang="html">
    <div class="radio-input"
        :class="{'has-error': error}">
        <div class="radio-option"
          v-if="listItems"
          v-for="(value, key) in listItems"
          :key="key"
        >
          <input type="radio"
            @click="updated($event.target.value)"
            v-model="currentValue"
            :disabled="!active"
            :value="key"/>
            {{value}}
        </div>
        <small v-if="error" class="help-block">{{error}}</small>
    </div>
</template>

<script>
import inputElement from '../../mixins/inputElement.js'
import validatedElement from '../../mixins/validatedElement.js';

export default {
    mixins: [inputElement, validatedElement],

    props: {
        choices: Object
    },

    computed: {
        listItems () {
            if (!this.choices) {
                return null;
            }
            if (this.choices.length === undefined) {
                return this.choices;
            }
            const rv = {};
            this.choices.forEach((choice) => {
                rv[choice] = choice;
            })
            return rv;
        }
    }
}
</script>
<style lang="css" scoped>
.radio-option {
    padding: 2.5px
}
</style>



