<template lang="html">
    <div class="annex-vue" ref="annexref">
        annex {{hash}}
    </div>
</template>

<script>
export default {
    data () {
        return { };
    },
    props: {
        corsurls: Array,
        hash: String,
        callback: Function
    },
    async mounted () {
        console.log('task', this.task)
        console.log('prop hash', this.hash);
        console.log('prop corsurls', this.corsurls);
        const isGigProd = !/qa|localhost/.test(window.location.host);

        const annexConfigEndpoint = isGigProd
            ? `https://annex.blpprofessional.com/service/hash-config/${this.hash}`
            : `https://annex.dev.blpprofessional.com/service/hash-config/${this.hash}`;
        const annexConfig = await window.fetch(annexConfigEndpoint).then(res => res.json())
        console.log('config from annex', annexConfig);

        // This is to solve the CORS document/odfoa loading
        // If your hash contains already CORS-enabled urls, this is not needed;
        // Otherwise, suppose your hash has entries: [{doc_url1, annotation_url1}, {doc_url2, annotation_url2}]
        // Then corsurls = [[cors_doc_url1, annotation_url1], [cors_doc_url2, annotation_url2]]
        const corsurls = this.corsurls;
        console.log('corsurls', corsurls);
        if (annexConfig.entries && Array.isArray(corsurls)) {
            for (let i = 0; i < corsurls.length; i++) {
                const [corsDocUrl, corsAnnotationUrl] = corsurls[i];
                const entry = annexConfig.entries[i];

                entry.doc_url = corsDocUrl || entry.doc_url;
                entry.annotation_url = corsAnnotationUrl || entry.annotation_url;
            }
        }

        // This is the new Annex library,
        // You define a `load` command on window
        // Then load teh loader.bundle.js
        // the loader will check the `load` command and load proper assets
        // and start launch sequence of the config behind annex hash
        window.Annex = [];
        window.Annex.push(['load', {
            env: isGigProd ? 'production' : 'development',
            useCdn: true,
            context: {
                annex_context: {
                    env: isGigProd ? 'production' : 'development',
                    hash_object: annexConfig
                }
            },
            user: 'gigwork',
            session: encodeURIComponent(window.location),
            annexHolder: this.$refs.annexref,
            successCallback: (result) => {
                console.log('success callback', result);
                const {docx} = result;
                const activeTab = docx.activeTab();
                activeTab.events.didPoint.registerCallback(e => this.$emit('didpoint', e));
                activeTab.events.annotationModelDidUpdate.registerCallback(() => this.$emit('odfoa', activeTab._internal.model.annotation.toObject()))
                this.$emit('docx', docx);
            },
            failureCallback: (ex) => {
                console.log('failure', ex);
            }
        }]);
        const s = document.createElement('script');
        s.src = isGigProd
            ? 'https://annex.blpprofessional.com/loader/loader.bundle.js'
            : 'https://annex.dev.blpprofessional.com/loader/loader.bundle.js';
        document.head.appendChild(s);
    }
}
</script>



