<template lang="html">
    <div class="multi-select-input"
        :class="{'has-error': error}">
        <multiselect
            :value="currentValue"
            @input="updated($event)"
            :options="choices"
            :multiple="true"
            :close-on-select="false"
            :clear-on-select="false"
            :preserve-search="true"
            :searchable="true"
            placeholder="select"
            :preselect-first="true"
          >
          </multiselect>
        <small v-if="error" class="help-block">{{error}}</small>
    </div>
</template>

<style src="../../css/multiselect_overwrite_style.css"></style>
<script>
import inputElement from '../../mixins/inputElement.js'
import validatedElement from '../../mixins/validatedElement.js';
import Multiselect from 'vue-multiselect';

export default {
    components: { Multiselect },
    mixins: [inputElement, validatedElement],
    props: ['choices']
}
</script>



