<template lang="html">
    <div class="instructions-panel">
        <button class="btn btn-primary"
                @click="toggle">
            {{`${(showInstructions ? "Hide" : "Show")} ${buttonText}`}}
        </button>
        <div class="instructions-content">
            <slot v-if="showInstructions"></slot>
        </div>
    </div>
</template>

<script>
import { mapModuleState } from '../../store/util'
import { getNamespace } from '../../bus'

export default {
    props: {
        buttonText: {
            type: String,
            default: 'Instructions'
        }
    },

    data () {
        return {
            showInstructions: true
        }
    },

    methods: {
        shouldShow () {
            return !window.localStorage.getItem(this.storageKey);
        },

        toggle () {
            this.showInstructions = !this.showInstructions;
            if (this.showInstructions) {
                window.localStorage.removeItem(this.storageKey);
            } else {
                window.localStorage.setItem(this.storageKey, true);
            }
        }
    },

    computed: {
        ...mapModuleState(
            getNamespace,
            [
                'projectName'
            ]
        ),

        storageKey () {
            return `hide_instructions_${this.projectName}`;
        }
    },

    watch: {
        projectName () {
            this.showInstructions = this.shouldShow();
        }
    }
}
</script>

<style lang="css">
.instructions-content {
    margin: 1em 0;
}
</style>



