<template lang="html">
    <div class="checkbox-input">
        <input type="checkbox"
               :checked="currentValue"
               :disabled="!active"
               @change="updated($event.target.checked)">
    </div>
</template>

<script>
import inputElement from '../../mixins/inputElement.js';

export default {
    mixins: [inputElement],

    props: {
        initialValue: {
            default: false
        }
    }
}
</script>



