<template lang="html">
    <div id="task-presenter">
        <alert-banner></alert-banner>

        <div v-if="goldAnswers">
            <slot></slot>
            <button class="btn btn-primary" @click="finishTask">Next Question</button>
        </div>
        <div v-else>

            <slot></slot>

            <div class="row presenter-row">
                <div class="col-xs-12">
                    <button-row :allow-save-work="allowSaveWork" :allow-assign-to-user="allowAssignToUser"></button-row>
                </div>
            </div>

            <span v-if="autoSaveSeconds">
                <auto-save :save-interval="autoSaveSeconds" :allow-assign-to-user="allowAssignToUser"></auto-save>
            </span>
            <div class="row presenter-row" v-if="autoSaveSeconds || allowSaveWork">
                <div class="col-xs-6">
                    <last-save-time></last-save-time>
                </div>
            </div>

            <div class="row presenter-row">
                <div class="col-xs-6">
                    <user-progress></user-progress>
                </div>
            </div>

        </div>
    </div>
</template>

<script>
import UserProgress from './UserProgress.vue'
import ButtonRow from './ButtonRow.vue'
import AlertBanner from './misc/AlertBanner.vue'
import ProjectStatus from './misc/ProjectStatus.vue'
import LastSaveTime from './misc/LastSaveTime.vue'
import AutoSave from './misc/AutoSave'
import { mapModuleGetters, mapModuleActions, mapModuleState } from '../store/util'
import { HAS_ALERT } from '../store/getters'
import { FINISH_TASK } from '../store/actions'
import { getNamespace } from '../bus'

export default {
    props: {
        autoSaveSeconds: {
            type: Number,
            default: 0
        },
        allowSaveWork: {
            type: Boolean,
            default: false
        },
        allowAssignToUser: {
            type: Boolean,
            default: false
        }
    },

    computed: {
        ...mapModuleGetters(
            getNamespace,
            {
                hasAlert: HAS_ALERT
            }
        ),

        ...mapModuleState(
            getNamespace,
            [
                'goldAnswers'
            ]
        )
    },

    methods: {
        ...mapModuleActions(
            getNamespace,
            {
                finishTask: FINISH_TASK
            }
        )
    },

    components: {
        ButtonRow,
        UserProgress,
        AlertBanner,
        ProjectStatus,
        AutoSave,
        LastSaveTime
    }
}
</script>

<style lang="css" scoped>
.presenter-row {
    margin-bottom: 1em;
}
</style>



