<template lang="html">
    <div class="text-tagging">
        <div class="tagging-area" ref="tagging-area" @mouseup="mouseup">
            <p><highlight-display-segment
                    v-if="tagStyle === 'highlight'"
                    v-for="(ds, i) in displaySegments"
                    :key="i"
                    :text="ds.text"
                    :tag="getTag(ds)"
                    @edit="editTag(i, $event)"/><bracket-display-segment
                    v-if="tagStyle === 'bracket'"
                    v-for="(ds, i) in displaySegments"
                    :key="i"
                    :displaySegment="ds"
                    :segments="segments"
                    :tags="tags"
                    @edit="editTag(i, $event)"/></p>
        </div>
        <div v-if="currentSelection"
            ref="tag-menu"
            :style="{ left: `${menuX}px`, top: `${menuY}px` }"
            class="panel panel-default tag-menu">
            <div class="panel-heading">
                <span v-if="!readOnly">Select Entity For: </span><strong>{{currentSelection.text}}</strong>
            </div>
            <div class="panel-body" v-if="!readOnly">
                <div class="form-group">
                    <div v-if="withComment">
                        <label>{{commentLabel}}<input class="form-control" type="text"
                                                      v-model="currentSelection.comment"></label>
                    </div>
                    <div class="checkbox">
                        <label>
                            <input type="checkbox" v-model="currentSelection.notSure">
                            Check if not sure about the annotation
                        </label>
                    </div>
                </div>
                <div class="list-group">
                    <label v-for="(tag, key) in tags" :key="key">
                        <input type="checkbox" :checked="currentSelection.tags.has(key)"
                               @change="tagCurrentSelection(key, $event)" :disabled="readOnly"/>
                        {{ tag.display }}
                    </label>
                </div>
            </div>
            <div v-if="!readOnly" class="panel-body pull-right">
                <button
                    class="btn btn-warning"
                    @click="removeTag()">Delete
                </button>
            </div>
        </div>
        <form class="controls">
            <label><input type="radio" name="style" value="highlight" :checked="tagStyle === 'highlight'"
                          v-model="tagStyle"/> Highlight</label>
            <label><input type="radio" name="style" value="bracket" :checked="tagStyle === 'bracket'"
                          v-model="tagStyle"/> Bracket</label>
            <label><input type="checkbox" v-model="verbose"> Verbose</label>
            <label><input type="checkbox" v-model="debug"> Debug</label>
        </form>
        <div v-if="verbose" class="verbose">
            <table>
                <tr>
                    <th>Index</th>
                    <th>Text</th>
                    <th>Start</th>
                    <th>End</th>
                    <th>Tags</th>
                    <th>Not Sure</th>
                    <th>Comment</th>
                </tr>
                <tr v-for="(segment, i) in segments" :key="i">
                    <td>{{ i }}</td>
                    <td>{{ segment.text }}</td>
                    <td>{{ segment.offset }}</td>
                    <td>{{ segment.endOffset }}</td>
                    <td>
                        <ul :style="{ 'margin': 0, 'padding': 0, 'list-style': 'none' }">
                            <li v-for="(tagKey, i) in segment.tags" :key="i"><span
                                :style="{ 'font-weight': 'bolder', 'color': tags[tagKey].color }">&#9640;</span> {{
                                    tags[tagKey].display }}</li>
                        </ul>
                    </td>
                    <td>{{ segment.notSure }}</td>
                    <td>{{ segment.comment }}</td>
                </tr>
            </table>
        </div>
        <pre v-if="debug" class="debug">{{ JSON.stringify(this.$data, null, 2) }}</pre>
    </div>
</template>

<script>
import HighlightDisplaySegment from './HighlightDisplaySegment.vue';
import BracketDisplaySegment from './BracketDisplaySegment.vue';
import inputElement from '../../mixins/inputElement.js'
import utils from './utils.js'

export default {
    mixins: [inputElement],

    props: {
        text: {
            type: String,
            default: ''
        },
        tags: {
            type: Object,
            default: () => ({
                LOC: {
                    display: 'LOC',
                    color: '#20B2AA'
                },
                ORG: {
                    display: 'ORG',
                    color: '#DAA520'
                },
                PER: {
                    display: 'PER',
                    color: '#F08080'
                },
                MISC: {
                    display: 'MISC',
                    color: '#87CEFA'
                }
            })
        },
        readOnly: {
            type: Boolean,
            default: false
        },
        nlpnedEntities: {
            type: Array,
            default: () => []
        },
        confidenceThreshold: {
            type: Number,
            default: 0,
            validator: function (value) {
                const valid = value >= 0 && value <= 1;
                if (!valid) {
                    console.warn(`Confidence threshold must be a number between 0 and 1; got ${value}`);
                }
                return valid;
            }
        },
        withComment: {
            type: Boolean,
            default: false
        },
        commentLabel: {
            type: String,
            default: 'Comment'
        },
        commentField: {
            type: String,
            default: 'comment'
        },
        defaultTagStyle: {
            type: String,
            default: 'highlight',
            validator (value) {
                return ['highlight', 'bracket'].includes(value)
            }
        },
        defaultVerbose: {
            type: Boolean,
            default: false
        }
    },

    mounted: function () {
        const onMouseDown = document.onmousedown;
        document.onmousedown = (evt) => {
            try {
                onMouseDown(evt);
            } catch (ignore) {
                // No-op
            }
            const menu = this.$refs['tag-menu'];
            if (!menu) {
                return;
            }
            if (!menu.contains(evt.target)) {
                this.clearCurrentSelection();
            }
        }

        this.initialize(this.text, this.nlpnedEntities);
    },

    data () {
        const {segments, displaySegments, forwardIndex} = utils.splitAndIndex(this.text, []);
        return {
            segments: segments,
            displaySegments: displaySegments,
            forwardIndex: forwardIndex,
            currentSelection: null,
            notSure: false,
            comment: '',
            menuX: -1,
            menuY: -1,
            tagStyle: this.defaultTagStyle ? this.defaultTagStyle : 'highlight',
            verbose: this.defaultVerbose,
            debug: false
        }
    },

    methods: {
        newSegment (offset, endOffset, tags) {
            return {
                text: this.text.substring(offset, endOffset),
                offset,
                endOffset,
                tags,
                notSure: this.notSure,
                comment: this.comment
            };
        },

        splitAndIndex () {
            // Omit deleted segments, and segments without any tags first
            const cleanSegments = this.segments.filter(s => s !== undefined && s.tags.size > 0);
            const {segments, displaySegments, forwardIndex} = utils.splitAndIndex(this.text, cleanSegments);
            this.segments = segments;
            this.displaySegments = displaySegments;
            this.forwardIndex = forwardIndex;
        },

        mapDisplaySegmentToSegment (displaySegment) {
            const segmentIndices = displaySegment['segmentIndices'];
            if (segmentIndices.length > 0) {
                // This decides the highlight stacking over. Make it simple, and let it
                // just be the addition order
                return this.segments[Math.max(...segmentIndices)];
            } else {
                return undefined;
            }
        },

        getSegment (start, end) {
            const segmentIndex = this.forwardIndex.getOrDefault(start, {})[end];
            return segmentIndex === undefined ? undefined : this.segments[segmentIndex];
        },

        addSegment (segment) {
            this.segments.push(segment);
            this.splitAndIndex();
        },

        removeSegment (segment) {
            const segmentIndex = this.forwardIndex.getOrDefault(segment.offset, {})[segment.endOffset];
            this.segments[segmentIndex] = undefined;
            this.splitAndIndex();
        },

        tagCurrentSelection (tagKey, event) {
            const segment = this.getSegment(this.currentSelection.offset, this.currentSelection.endOffset);

            // Note that the tag set is recreated for Vue reactivity
            if (event.currentTarget.checked) {
                if (segment === undefined) {
                    this.currentSelection.tags.add(tagKey);
                    this.addSegment(this.currentSelection);
                } else {
                    segment.tags = new Set(segment.tags);
                    segment.tags.add(tagKey);
                }
            } else {
                if (segment === undefined) {
                    this.currentSelection.tags = new Set(this.currentSelection.tags);
                    this.currentSelection.tags.delete(tagKey);
                } else {
                    segment.tags = new Set(segment.tags);
                    segment.tags.delete(tagKey);
                    if (segment.tags.size === 0) {
                        this.removeSegment(segment);
                    }
                }
            }
        },

        editTag (displaySegmentIndex, event) {
            // Don't open edit tag dialog if there's an active highlight; the user clicked on this segment
            // to start a new highlight that overlaps with this segment
            const {startOffset, endOffset} = this.getOffsets();
            if (startOffset !== endOffset) {
                return;
            }
            if (this.displaySegments[displaySegmentIndex].segmentIndices.length === 0) {
                return;
            }
            this.setCurrentSelection(displaySegmentIndex);
            this.setMenuPosition(event.target);
        },

        removeTag () {
            const segment = this.getSegment(this.currentSelection.offset, this.currentSelection.endOffset);
            this.clearCurrentSelection();
            if (segment !== undefined) {
                this.removeSegment(segment);
            }
        },

        getCurrentRange () {
            const selection = getSelection();
            return selection.getRangeAt(0);
        },

        getOffsets () {
            // get start and end offsets for the current selection
            const range = this.getCurrentRange();
            const r = range.cloneRange();
            r.selectNodeContents(this.$refs['tagging-area']);
            r.setEnd(range.endContainer, range.endOffset);
            const endOffset = r.toString().length;
            const startOffset = endOffset - range.toString().length
            return { startOffset, endOffset };
        },

        setCurrentSelection (displaySegmentIndex, offset, endOffset) {
            // set the segment for menu display.
            // if displaySegmentIndex is < 0, the selection will create a new segment
            if (displaySegmentIndex >= 0) {
                this.currentSelection = this.mapDisplaySegmentToSegment(this.displaySegments[displaySegmentIndex]);
            } else {
                this.currentSelection = this.getSegment(offset, endOffset);
                if (this.currentSelection === undefined) {
                    this.currentSelection = this.newSegment(offset, endOffset, new Set());
                }
            }
        },

        clearCurrentSelection () {
            this.currentSelection = undefined;
        },

        mouseup (event) {
            if (this.readOnly) {
                return;
            }
            // Limit to left mouse button click
            // FIXME - Potential accessibility problem for left-handed users?
            if (event.button !== 0) {
                return;
            }

            const { startOffset, endOffset } = this.getOffsets();
            if (startOffset === endOffset) {
                return;
            }

            this.setCurrentSelection(-1, startOffset, endOffset);
            this.setMenuPosition();
        },

        setMenuPosition (element) {
            element = element || this.getCurrentRange();
            const areaBox = this.$refs['tagging-area'].getBoundingClientRect();
            const elementBox = element.getBoundingClientRect();
            this.menuX = elementBox.left - areaBox.left;
            this.menuY = elementBox.bottom - areaBox.top;
        },

        getTag (displaySegment) {
            const segment = this.mapDisplaySegmentToSegment(displaySegment);
            if (segment === undefined) {
                return undefined;
            } else {
                const tags = segment.tags;
                const color = tags.size === 1 ? 'initial' : 'white';
                const backgroundColor = tags.size === 1 ? this.tags[tags.values().next().value].color : 'black';
                const display = tags.size === 1 ? this.tags[tags.values().next().value].display
                    : Array.from(tags).map(tagKey => this.tags[tagKey].display).join(', ');
                return {color, backgroundColor, display};
            }
        },

        initialize (text, entities) {
            this.segments = entities
                .filter(ent => !ent.candidates || !ent.candidates.length ||
                    (ent.candidates[0].confidence || 0) >= this.confidenceThreshold)
                .map(ent => ent.recognized)
                .map(({headoffset, tailoffset, taggedtype}) => this.newSegment(headoffset, tailoffset, new Set([taggedtype])));

            this.splitAndIndex();
            this.updated(this.tagged);
        },

        _resetValue () {
            this.initialize(this.text, this.nlpnedEntities);
        }
    },

    watch: {
        segments () {
            this.updated(this.tagged);
        },
        currentSelection () {
            this.updated(this.tagged);
        },
        text (text, oldText) {
            if (text === oldText) {
                return;
            }
            this.initialize(text, this.nlpnedEntities);
        },

        nlpnedEntities (newVal, oldVal) {
            if (JSON.stringify(newVal) === JSON.stringify(oldVal)) {
                return;
            }
            this.initialize(this.text, newVal);
        }
    },

    computed: {
        tagged () {
            return this.segments
                .map(s => {
                    const taggedSegment = {
                        tags: s.tags,
                        offset: s.offset,
                        endOffset: s.endOffset,
                        text: s.text,
                        notSure: s.notSure
                    };
                    if (this.withComment) {
                        taggedSegment[this.commentField] = s.comment;
                    }
                    return taggedSegment;
                });
        }
    },
    updated () {
        if (this.tagStyle !== 'bracket') {
            return;
        }
        // Spread the lines so the brackets for one line do not cover text in the next line. This must happen in the
        // post-render hook because the lines on which the display segments fall are difficult to pre-compute (but all at the
        // mercy of the browser's text wrapping). When the component finishes rendering, scan all the display segments and
        // map them to the lines. Then, for each line, compute the max depth of overlap, and vertically spread the
        // lines and the brackets proportionately. Do direct DOM operations instead of Vue state changes.
        // TODO - Could be expensive? Debounce or run only if segment changes?

        // Strip old line spacers first
        for (const $lineSpacer of this.$refs['tagging-area'].querySelectorAll('.tagging-area > p .line-spacer')) {
            $lineSpacer.remove();
        }

        const $displaySegments = this.$refs['tagging-area'].querySelectorAll('.tagging-area > p .display-segment.bracket');
        const lines = [];
        const maxOverlapDepthPerLine = []
        let currentLineIndex = -1;
        let prevY = Number.NEGATIVE_INFINITY;

        // Map display segments to lines
        for (const $displaySegment of $displaySegments.values()) {
            const y = $displaySegment.getBoundingClientRect().top;
            if (prevY < y) {
                prevY = y;
                currentLineIndex++;
                lines.push([]);
                maxOverlapDepthPerLine.push(0);
            }
            lines[currentLineIndex].push($displaySegment);
            const overlapDepth = parseInt($displaySegment.dataset['numSegments']);
            if (maxOverlapDepthPerLine[currentLineIndex] < overlapDepth) {
                maxOverlapDepthPerLine[currentLineIndex] = overlapDepth;
            }
        }
        lines.forEach((line, index) => {
            // Spread lines
            const $lineSpacer = document.createElement('span');
            $lineSpacer.className = 'line-spacer';
            $lineSpacer.style.height = (maxOverlapDepthPerLine[index] + 2) + 'em';
            line[0].insertAdjacentElement('beforeend', $lineSpacer);

            // Spread brackets
            const maxOverlapDepth = maxOverlapDepthPerLine[index];
            const heights = new Array(maxOverlapDepth + 1);
            for (const $displaySegment of line) {
                const $brackets = Array.from($displaySegment.querySelectorAll('.bracket').values());
                const sIndices = new Set($brackets.map($b => $b.dataset['segmentIndex']));
                for (const $bracket of $brackets) {
                    let availableHeight = Number.POSITIVE_INFINITY;
                    const segmentIndex = $bracket.dataset['segmentIndex'];
                    let heightForBracket;
                    for (let height = 1; height < heights.length; height++) {
                        const sIndexAtHeight = heights[height];
                        if (sIndexAtHeight === segmentIndex) {
                            heightForBracket = height;
                            break;
                        } else if (height < availableHeight &&
                            (sIndexAtHeight === undefined || !sIndices.has(sIndexAtHeight))) {
                            availableHeight = height;
                        }
                    }
                    if (heightForBracket === undefined) {
                        heights[availableHeight] = segmentIndex;
                        heightForBracket = availableHeight;
                    }
                    $bracket.style.height = heightForBracket + 'em';
                }
            }
        });
    },
    components: {
        HighlightDisplaySegment,
        BracketDisplaySegment
    }
}
</script>

<style lang="css" scoped>
.text-tagging {
    box-shadow: 0 0 2px;
    position: relative;
    padding: 1rem 2rem;
}

.text-tagging > * + * {
    margin-top: 1rem;
}

.text-tagging p {
    padding: 0;
    margin: 0
}

.tag-menu {
    position: absolute;
    z-index: 3000;
    min-width: 10em;
    max-width: 25em;
    box-shadow: 2px 2px 2px
}

.tag-menu > .panel-body .form-group {
    margin: 0;
}

.text-tagging > .tagging-area > p >>> .display-segment,
.text-tagging > .verbose > table td,
.text-tagging > .tag-menu strong {
    /* Prevents white-spacing collapsing at span edges. For example, without this,
     the trailing space in <span>foo </span> does not render on the browser*/
    white-space: pre-wrap;
}

.text-tagging > .tagging-area > p >>> .display-segment.tagged-text:hover {
    cursor: pointer;
}

.text-tagging > .tagging-area > p >>> .line-spacer {
    display: inline-block;
    vertical-align: text-top;
}

.text-tagging > .tag-menu > .panel-body > .list-group > label {
    width: 100%;
}

.text-tagging > .controls > label + label {
    margin-left: 1rem;
}

.text-tagging > .controls > label > input {
    vertical-align: top;
}

.text-tagging > .verbose > table {
    min-width: 64rem;
}

.text-tagging > .verbose > table th, .text-tagging > .verbose > table td {
    border: 1px solid;
    border-collapse: collapse;
    padding: 2px 4px;
}
</style>



