<template lang="html">
    <div class="dropdown-input"
        :class="{'has-error': error}">
        <select class="form-control"
            :value="currentValue"
            @input="updated($event.target.value)"
            placeholder="select"
            :disabled="!active">
            <option v-if="listItems"
                    v-for="(value, key) in listItems"
                    :key="key"
                    :value="key">{{value}}
            </option>
            <slot v-if="!listItems"></slot>
        </select>
        <small v-if="error" class="help-block">{{error}}</small>
    </div>
</template>

<script>
import inputElement from '../../mixins/inputElement.js'
import validatedElement from '../../mixins/validatedElement.js';

export default {
    mixins: [inputElement, validatedElement],

    props: ['choices'],

    computed: {
        listItems () {
            if (!this.choices) {
                return null;
            }
            if (this.choices.length === undefined) {
                return this.choices;
            }
            const rv = {};
            this.choices.forEach((choice) => {
                rv[choice] = choice;
            })
            return rv;
        }
    }

}
</script>



