<template lang="html">
    <base-button
        class="submit-last-button"
        :btnClass="'btn btn-warning'"
        @click.native="submitTask">
        Submit and Leave
    </base-button>
</template>

<script>
import submitButtonMixin from '../../mixins/submitButton.js';
import BaseButton from './BaseButton.vue';

export default {
    data () {
        return { last: true }
    },

    mixins: [submitButtonMixin],

    components: { BaseButton }
}
</script>

<style lang="css">
</style>



