// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
'use strict'
import Vue from 'vue'
import * as mutations from './store/mutations.js'
import * as actions from './store/actions'
import inputElement from './mixins/inputElement'
import { runValidations } from './validation.js'

import {
    mapModuleState,
    mapModuleActions,
    mapModuleMutations,
    setTableAnswer,
    setSingleAnswer
} from './store/util'

import components from './components.js'
import { inputBus, getNamespace } from './bus.js'
import setupHooks from './hooks'

import { fetchPrivateJson, fetchEncryptedJson } from './fileFetch'

const pybossa = window.pybossa;
setupHooks(window.pybossa);

// Define callable function to plug-in components
pybossa.useComponent = function (componentName, componentFactory) {
    const component = Vue.component(
        componentName,
        componentFactory({ inputElement }, components)
    )
    components[componentName] = component;
}

Vue.config.productionTip = false

const messages = {
    taskSubmitted: {
        text: 'Your answer has been saved',
        duration: 2000
    }
}
/* eslint-disable no-new */
function createApp (projectName, store) {
    return new Vue({
        data: {
            config: {},
            task: { id: null, info: {} },
            answer: {},
            taskDoneDeferred: null
        },

        store,

        mounted () {
            const vm = this;
            let isFirstTask = true;
            let isTaskAssigned = false;
            // set input event callback
            inputBus.$on('input', ({name: path, value: answer}) => {
                vm.answer = setSingleAnswer(vm.answer, path, answer);
            });

            inputBus.$on('table-input', ({tableName, idColName, rowId, colName, value: answer}) => {
                const {answers} = setTableAnswer(vm.answer, tableName, idColName, rowId, colName, answer);
                vm.answer = answers;
            });

            inputBus.$on('delete-table-input', ({tableName, idColName, rowId}) => {
                vm.answer[tableName].splice(vm.answer[tableName].findIndex(item => item[idColName] === rowId), 1)
            });

            inputBus.$on('add-table-input', ({tableName, obj}) => {
                if (!vm.answer[tableName]) {
                    vm.answer[tableName] = [];
                }
                vm.answer[tableName].push(obj)
            });

            inputBus.$on('save-answers', async () => {
                const answer = JSON.parse(JSON.stringify(vm.answer));
                try {
                    await pybossa.savePartialAnswer(vm.task.id, answer);
                    vm.setLastSaveTime(new Date());
                } catch (err) {
                    console.warn({ err });
                }
            });

            inputBus.$on('assign-user', async () => {
                if (!isTaskAssigned) {
                    isTaskAssigned = true;
                    try {
                        await pybossa.assignTaskToUser(this.projectName, this.task.id);
                    } catch (err) {
                        console.warn({ err });
                    }
                }
            });

            this.initialSetUp(this.config);

            // set the present task callback
            pybossa.presentTask(onPresentTask);
            this.setProjectName({ projectName });
            // start the loop
            pybossa.run(projectName);

            async function onPresentTask (task, taskDoneDeferred) {
                // After a task has been assigned we need this in order to get
                // the correct project status. User only enters quiz mode
                // when a task has been assigned.
                if (isFirstTask) {
                    await vm.setGoldAnswers(null);
                    isFirstTask = false;
                }

                vm.taskDoneDeferred = taskDoneDeferred;

                if (!Object.keys(task).length) {
                    return vm.newTask({ taskId: task.id });
                }
                await fetchPrivateJson(task);
                await fetchEncryptedJson(task);
                await vm.beforePresent(task, vm.config);
                // This must come after `beforePresent` so that the reactivity system will
                // detect any properties added or removed from `task`.
                vm.task = task;

                vm.newTask({ taskId: task.id });
                const savedWork = await pybossa.getSavedAnswer(task.id);
                if (savedWork) {
                    const { answer, savedAt } = savedWork;
                    vm.setAnswer(answer);
                    vm.answer = JSON.parse(JSON.stringify(answer));
                    vm.setLastSaveTime(new Date(savedAt));
                }
                vm.setActive();
            }
        },

        methods: {
            ...mapModuleMutations(
                getNamespace,
                {
                    setMessage: mutations.SET_MESSAGE,
                    setActive: mutations.SET_ACTIVE,
                    setProjectName: mutations.SET_PROJECT_NAME,
                    setAnswer: mutations.SET_ANSWERS,
                    setLastSaveTime: mutations.SET_LAST_SAVE_TIME
                }
            ),

            ...mapModuleActions(
                getNamespace,
                {
                    newTask: actions.NEW_TASK,
                    setGoldAnswers: actions.SET_GOLD_ANSWERS
                }
            ),

            initialSetUp (config) {
                pybossa.hooks.setUp(config);
            },

            async beforePresent (task, config) {
                return Promise.resolve(pybossa.hooks.beforePresent(task, config));
            },

            async beforeSubmit (answer, task, config) {
                return Promise.resolve(pybossa.hooks.beforeSubmit(answer, task, config));
            },

            async submit () {
                const taskId = this.task.id;
                try {
                    runValidations();
                    const answer = await this.beforeSubmit(this.answer, this.task, this.config);
                    const res = await pybossa.saveTask(this.task, answer);
                    this.setMessage(messages.taskSubmitted);
                    const goldAnswers = res ? res.goldAnswers : {};
                    await this.setGoldAnswers(goldAnswers);
                    this.afterSubmit();
                    pybossa.deleteSavedAnswer(taskId);
                } catch (error) {
                    this.setMessage({
                        text: `There was an error: ${error.message}`,
                        type: 'alert-danger'
                    });
                    this.setActive();
                }
                window.scrollTo(0, 0);
            },

            afterSubmit () {
                // clear taskrun
                this.answer = {};
                if (this.leaving) {
                    window.location.replace(`/project/${this.projectName}`);
                    pybossa.releaseCategoryLocks(this.projectName, this.task.id);
                } else {
                    this.taskDoneDeferred.resolve();
                }
            }
        },

        computed: {
            ...mapModuleState(
                getNamespace,
                [
                    'submitFlag',
                    'leaving',
                    'projectName'
                ]
            )
        },

        watch: {
            submitFlag (newVal) {
                if (!newVal) {
                    return;
                }
                this.submit();
            }
        },

        components: components
    })
}

export default createApp;



